import React, { useState } from 'react';
import Header from './Header';
import img2 from '../../media/2.jpg';
import p1 from '../../media/p1.jpg';


export default function(props) {

    const [widthRl, setWidthRl] = useState(0);

    const styleHead = {
        backgroundImage: `url(${img2})`
    }

    const styleReadLine = { width: `${widthRl}px`};

    const scrollHandler = e => {
        const content = document.querySelectorAll('.content > *');
        content.forEach(elem => {
            if (elem.getBoundingClientRect().y < window.innerHeight / 1.2) {
                elem.classList.remove('disable');
                elem.classList.add('active');
            };
        });
        const percent = (e.target.scrollTop / ((e.target.scrollHeight - window.innerHeight) / 100));
        setWidthRl(percent / 100 * window.innerWidth);
    }

    return (
        <div className="post" onScroll={scrollHandler}>
            <div className="read-line" style={styleReadLine}></div>
            <Header/>
            <div className="head" style={styleHead}>
                <div className="head-wrapper">
                    <div className="tag active"><h3>WEB DESIGN</h3></div>   
                    <div className="title active"><h1>THE COOLIDGE THEATER</h1></div>
                </div>
            </div>
            <div className="content">
                <div className="headline container disable">
                    <h1>THE COOLIDGE THEATER</h1>
                </div>
                <div className="paragraph container disable">
                    <p>Комедийно-детективный мультсериал «Арчер», созданный Адамом Ридом, рассказывает о деятельности международного шпионского агентства, членом которого является Стерлинг Арчер. Он довольно легкомысленная личность, врун и самый настоящий хам, который не пропустит ни одной юбки. По правде говоря, звание специального агента Арчер получил лишь только благодаря тому, что его мать является главой того самого агентства. Кроме него здесь ещё трудиться множество секретных агентов, которые не просто так едят свой хлеб, и на самом деле являются профессионалами высшего уровня. Для каждого из них любой мировой кризис это всего лишь повод посоревноваться между собой…</p>
                </div>
                <div className="panorama disable">
                    <img src={p1} alt="#"/>
                </div>
                <div className="paragraph container disable">
                    <p>Комедийно-детективный мультсериал «Арчер», созданный Адамом Ридом, рассказывает о деятельности международного шпионского агентства, членом которого является Стерлинг Арчер. Он довольно легкомысленная личность, врун и самый настоящий хам, который не пропустит ни одной юбки. По правде говоря, звание специального агента Арчер получил лишь только благодаря тому, что его мать является главой того самого агентства. Кроме него здесь ещё трудиться множество секретных агентов, которые не просто так едят свой хлеб, и на самом деле являются профессионалами высшего уровня. Для каждого из них любой мировой кризис это всего лишь повод посоревноваться между собой…</p>
                </div>
                <div className="image container disable">
                    <img src={img2} alt="#"/>
                </div>

            </div>
        </div>
    );
}