import React from 'react';
import { Typography } from 'antd';

const { Paragraph } = Typography;

export default function(props) {
    const { activePost: { id, preview, backview, title, tag, excerpt }, postsCount, currentNumber, anim, parallax } = props.data;

    const stylePreview = { backgroundImage: `url(${preview})`, transform: `translate(${parallax.mid.x}px, ${parallax.mid.y}px)` };
    const styleBackview = { backgroundImage: `url(${backview})`, transform: `translate(${parallax.slow.x}px, ${parallax.slow.y}px)` };
    const styleText = { transform: `translate(${parallax.fast.x}px, ${parallax.fast.y}px)` }

    return (
        <div className={`center ${anim}`}>
                <div className="preview" style={stylePreview}></div>
                <div className="backview" style={styleBackview}></div>
                <div className="text" style={styleText}>
                    <div className="title-wrapper">
                        <div className="tag"><h3>{tag}</h3></div>   
                        <div className="title"><h1>{title}</h1></div>
                    </div>
                    <div className="num"><h3><span>{currentNumber}</span> / {postsCount}</h3></div>
                    <div className="excerpt">    
                        <Paragraph ellipsis={{ rows: 4, expandable: false }}>
                            {excerpt}
                        </Paragraph></div>
                    <div className="btn"><span>ДАЛІ</span></div>
                </div>
        </div>
    );
}